import * as React from 'react';
import { Card, CardContent, Typography, Box } from '@material-ui/core';
import NoteIcon from '@material-ui/icons/Note';
import { makeStyles } from '@material-ui/core/styles';
import {
    useGetList,
    useGetIdentity,
    ReferenceField,
    TextField,
    FunctionField,
} from 'react-admin';
import { formatDistance } from 'date-fns';
import ReactMarkdown from 'react-markdown';
import remarkGfm from 'remark-gfm';


import { Contact as ContactType } from '../types';

const useStyles = makeStyles(theme => ({
    note: {
        marginBottom: theme.spacing(2),
    },
    noteText: {
        backgroundColor: theme.palette.grey[200],
        padding: theme.spacing(1),
        borderRadius: 10,
    },
    noteTextText: {
        display: '-webkit-box',
        '-webkit-line-clamp': 3,
        '-webkit-box-orient': 'vertical',
        overflow: 'hidden',
    },
}));

export const LatestNotes = () => {
    const classes = useStyles();
    const { identity } = useGetIdentity();
    const {
        data: contactNotesData,
        ids: contactNotesIds,
        loaded: contactNotesLoaded,
    } = useGetList(
        'contactNotes',
        { page: 1, perPage: 5 },
        { field: 'date', order: 'DESC' },
        { sales_id: identity?.id },
        { enabled: Number.isInteger(identity?.id) }
    );
    const {
        data: dealNotesData,
        ids: dealNotesIds,
        loaded: dealNotesLoaded,
    } = useGetList(
        'dealNotes',
        { page: 1, perPage: 5 },
        { field: 'date', order: 'DESC' },
        { sales_id: identity?.id },
        { enabled: Number.isInteger(identity?.id) }
    );
    const {
        data: companyNotesData,
        ids: companyNotesIds,
        loaded: companyNotesLoaded,
    } = useGetList(
        'companyNotes',
        { page: 1, perPage: 5 },
        { field: 'date', order: 'DESC' },
        { sales_id: identity?.id },
        { enabled: Number.isInteger(identity?.id) }
    );
    if (!contactNotesLoaded || !dealNotesLoaded || !companyNotesLoaded) {
        return null;
    }
    // TypeScript guards
    if (
        !contactNotesIds ||
        !contactNotesData ||
        !dealNotesIds ||
        !dealNotesData ||
        !companyNotesIds ||
        !companyNotesData
    ) {
        return null;
    }

    const allNotes = ([] as any[])
        .concat(
            contactNotesIds.map(id => ({
                ...contactNotesData[id],
                type: 'contactNote',
            })),
            dealNotesIds.map(id => ({
                ...dealNotesData[id], 
                type: 'dealNote' 
            })),
            companyNotesIds.map(id => ({
                ...companyNotesData[id], 
                type: 'companyNote' 
            })),
        )
        .sort((a, b) => new Date(b.date).valueOf() - new Date(a.date).valueOf())
        .slice(0, 5);

    return (
        <>
            <Box display="flex" alignItems="center" marginBottom="1em">
                <Box ml={2} mr={2} display="flex">
                    <NoteIcon color="disabled" fontSize="large" />
                </Box>
                <Typography variant="h5" color="textSecondary">
                    My Latest Notes
                </Typography>
            </Box>
            <Card>
                <CardContent>
                    {allNotes.map(note => (
                        <div
                            id={`${note.type}_${note.id}`}
                            key={`${note.type}_${note.id}`}
                            className={classes.note}
                        >
                            <Typography color="textSecondary" gutterBottom>
                                on{' '}
                                <NoteSource note={note} />
                                , added{' '}
                                {formatDistance(
                                    new Date(note.date),
                                    new Date(),
                                    {
                                        addSuffix: true,
                                    }
                                )}
                            </Typography>
                            <div className={classes.noteText}>
                                <ReactMarkdown children={note.text} remarkPlugins={[remarkGfm]} />
                            </div>
                        </div>
                    ))}
                </CardContent>
            </Card>
        </>
    );
};

const NoteSource = ({ note }: any) => {
    if (note.type === 'dealNote'){
        return <Deal note={note} />;
    }
    else if (note.type === 'contactNote') {
        return <Contact note={note} />;
    }
    else if (note.type === 'companyNote') {
        return <Company note={note} />;
    }
    return null;
};

const Deal = ({ note }: any) => (
    <>
        Deal{' '}
        <ReferenceField
            record={note}
            source="deal_id"
            reference="deals"
            basePath="/deals"
            link="show"
        >
            <TextField source="name" variant="body1" />
        </ReferenceField>
    </>
);

const Contact = ({ note }: any) => (
    <>
        Contact{' '}
        <ReferenceField
            record={note}
            source="contact_id"
            reference="contacts"
            basePath="/contacts"
            link="show"
        >
            <FunctionField<ContactType>
                variant="body1"
                render={contact =>
                    contact ? `${contact.first_name} ${contact.last_name}` : ''
                }
            />
        </ReferenceField>
    </>
);

const Company = ({ note }: any) => (
    <>
        Company{' '}
        <ReferenceField
            record={note}
            source="company_id"
            reference="companies"
            basePath="/companies"
            link="show"
        >
            <TextField source="name" variant="body1" />
        </ReferenceField>
    </>
);
